@charset "UTF-8";

.video-responsive {
    position: relative;
    padding-bottom: 56.25%;
    height: 0; /*360px*/
    overflow:hidden;
}

.video-responsive iframe,
.video-responsive object,
.video-responsive embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
